import { useNavigate, useParams } from "react-router-dom";

const Activity = ({ tagCodeToTitle, activities }) => {
  const navigate = useNavigate();
  const { id: currentId } = useParams();

  const selectedActivity = activities.find(({ index }) => index === +currentId);

  if (!selectedActivity) {
    alert('Активность не найдена!');
    return null;
  }

  const {
    title,
    description,
    tags,
    youtubeUrl,
    services,
    teasers,
  } = selectedActivity;

  return <div className="selected-activity">
    <button
      style={{
        cursor: 'pointer',
      }}
      onClick={() => {
        navigate('/activities');
      }}
    >
      ← Назад
    </button>

    <div>
      <h1>{title}</h1>
      <ul className="tags">
        {(tags || []).map((tag, index) => {return tagCodeToTitle[tag] && <li key={`${tag}_${index}`}>{tagCodeToTitle[tag]}</li>})}
      </ul>
    </div>
    <div className="modal-content">
      {youtubeUrl && (
        <iframe width="700" height="400" style={{ maxWidth: '100%' }} src={`https://www.youtube.com/embed/${youtubeUrl}`} title="YouTube" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
      )}
      <p>{description}</p>
      {services && (
        <>
          <h3>Сервисы</h3>
          <ul>
            {services.map(({ title, url }, index) => <li key={`${title}_${index}`}>
              {url
                ? <a href={url} target="_blank" rel="noreferrer">{title || url}</a>
                : title || url
              }
            </li>)}
          </ul>
        </>
      )}
      <h3>Тизеры</h3>
      <ul>
        {teasers?.map(({ title, url }, index) => <li key={`${title}_${index}`}>
          {url
            ? <a href={url} target="_blank" rel="noreferrer">{title || url}</a>
            : title || url
          }
        </li>)}
        <li><a href={`https://www.tiktok.com/search?q=${title.replace(' ', '+')}`} target="_blank" rel="noreferrer">Посмотреть в TikTok</a></li>
        <li><a href={`https://www.youtube.com/results?search_query=${title.replace(' ', '+')}`} target="_blank" rel="noreferrer">Посмотреть на YouTube</a></li>
      </ul>

      <h3>Подобрать вариант</h3>
      <ul>
        <li><a href={`https://yandex.ru/maps/?text=${title}`} target="_blank" rel="noreferrer">Показать на Яндекс.Картах</a></li>
      </ul>
      {/* <div className="json">
        {JSON.stringify(selectedActivity)}
      </div> */}
    </div>
  </div>;
}


export default Activity;
