import React, { useEffect, useState } from 'react';
import {
  Routes,
  Route,
  HashRouter,
} from "react-router-dom";
import firebase from "firebase/compat/app";
import { getDatabase, ref, child, get } from "firebase/database";
import "firebase/compat/auth";
import { propOr, all, uniq } from 'ramda';
import Filter from './pages/Filter';
import Activity from './pages/Activity';
import ActivitiesList from './pages/ActivitiesList';
import tagsGroupsJson from './data/tagsGroups';

const firebaseConfig = {
  apiKey: 'AIzaSyCExrlbwm2DMX_lrhS516-93vUf4GSPgAA',
  authDomain: "tryeverything-421ed.firebaseapp.com",
  databaseURL: "https://tryeverything-421ed-default-rtdb.europe-west1.firebasedatabase.app",
  projectId: "tryeverything-421ed",
  storageBucket: "tryeverything-421ed.appspot.com",
  messagingSenderId: "768977457633",
  appId: "1:768977457633:web:df590da29170678cdf42a0",
  measurementId: "G-56RPZMD413"
};

if (firebase.apps.length === 0) {
  firebase.initializeApp(firebaseConfig);
}

const dbRef = ref(getDatabase());

const now = new Date();
const currentMonth = now.getMonth();
const isSummer = currentMonth > 3 && currentMonth < 10;
const initialTags = uniq([
  'alone',
  'adults',
  ...JSON.parse(localStorage.getItem('selectedTags') || '[]'),
  'summer',
  'winter',
]).filter(tag => !((tag === 'summer' && !isSummer) || (tag === 'winter' && isSummer)));

const App = () => {
  const [activities, setActivities] = useState([]);
  const [tags, setTags] = useState([]);
  const [selectedTags, setSelectedTags] = useState(initialTags);

  const getAppData = () => {
    Promise.all([
      get(child(dbRef, '/activities/')),
      get(child(dbRef, '/tags/')),
    ]).then(([activitiesSnapshot, tagsSnapshot]) => {
      setActivities(activitiesSnapshot && activitiesSnapshot.val());
      setTags(tagsSnapshot && tagsSnapshot.val());
    });
  }

  useEffect(() => {
    getAppData();
  }, []);

  if (activities.length === 0 || tags.length === 0) {
    return null;
  }

  const tagCodeToTitle = tags.reduce((dict, tag) => ({ ...dict, [tag.code]: tag.title }), {});

  const filteredActivities = activities
    .map((activity, index) => ({...activity, index}))
    .filter((activity) => activity.title !== '-')
    .filter((activity) => selectedTags.length === 0 || all(tag => {
      const activityTags = propOr([], 'tags', activity);
      const tagsGroup = tagsGroupsJson.find((group) => group.items.includes(tag));
      if (tagsGroup.type === 'polar') {
        const otherTag = tagsGroup.items.filter(t => t !== tag)[0];
        return activityTags.includes(tag) || !activityTags.includes(otherTag);
      }
      return activityTags.includes(tag);
    }, selectedTags));

  const tagsGroups = tagsGroupsJson.map(group => ({
    ...group,
    items: group.items.map(code => ({
      code,
      title: tagCodeToTitle[code],
    })),
  }));
  const allTagsFromGroups = tagsGroups.reduce((total, group) => [...total, ...group.items], []);
  tagsGroups.push({
    code: 'rest',
    title: 'Другие категории',
    items: tags.filter(tag => !allTagsFromGroups.map(tag => tag.code).includes(tag.code)),
  });

  return (
    <div className="app">
      <HashRouter>
        <Routes>
          <Route path="activity">
            <Route path=":id" element={
              <Activity
                tagCodeToTitle={tagCodeToTitle}
                activities={activities}
              />
            } />
          </Route>
          <Route path="activities" element={
            <ActivitiesList
              filteredActivities={filteredActivities}
            />
          } />
          <Route path="/" element={
            <Filter
              tagsGroups={tagsGroups}
              selectedTags={selectedTags}
              setSelectedTags={(tags) => {
                localStorage.setItem('selectedTags', JSON.stringify(tags));
                setSelectedTags(tags);
              }}
              filteredActivities={filteredActivities}
            />
          } />
        </Routes>
      </HashRouter>
      <p>
        Есть предложения по улучшению сервиса? Например, улучшить функционал или дополнить список активностей. <a href="https://t.me/polyakovin" target="_blank" rel="noreferrer">Напиши об этом автору!</a> Он будет тебе за это очень благодарен :)
      </p>
    </div>
  );
}

export default App;
