import { complement, equals, prop } from 'ramda';

const Tags = ({ title, type, tags, selectedTags, setSelectedTags }) => {
  return <div>
    <h2>{title}</h2>
    {tags
      .filter(({ title }) => title !== '-')
      .map(({ code, title }, index) =>
        <button
          key={`${code}_${index}`}
          className={selectedTags.includes(code) ? 'selected' : ''}
          onClick={() => {
            if (selectedTags.includes(code)) {
              setSelectedTags(selectedTags.filter(complement(equals)(code)));
            } else {
              if (type === 'polar') {
                selectedTags = selectedTags.filter(tagCode => !tags.map(prop('code')).includes(tagCode));
              }
              setSelectedTags([...selectedTags, code]);
            }
          }
        }>
          {title}
        </button>)}
  </div>;
}

export default Tags;
