import { useNavigate } from 'react-router-dom';
import Tags from '../components/Tags';

const Filter = ({ tagsGroups, selectedTags, setSelectedTags, filteredActivities }) => {
  const navigate = useNavigate();
  return <>
    {tagsGroups.map(({ code, title, type, items }, index) => <Tags
      key={`${code}_${index}`}
      title={title}
      type={type}
      tags={items}
      selectedTags={selectedTags}
      setSelectedTags={setSelectedTags}
    />)}
    <div>
      <button
        style={{
          marginTop: '30px',
          padding: '20px',
          cursor: 'pointer',
        }}
        onClick={() => {
          navigate('/activities');
        }}
      >
        Показать { filteredActivities.length } активностей →
      </button>
    </div>
  </>;
}


export default Filter;
