import { useNavigate } from "react-router-dom";

const ActivitiesList = ({ filteredActivities }) => {
  const navigate = useNavigate();
  const goTo = (activity) => {
    navigate(`/activity/${activity.id}`);
  }

  return <>
    <button
      style={{
        cursor: 'pointer',
      }}
      onClick={() => {
        navigate('/');
      }}
    >
      ← Назад
    </button>

    <ol>
      {filteredActivities
        .sort((a, b) => a.title.toLowerCase() > b.title.toLowerCase() ? 1 : -1)
        .map(activity => <li key={`${activity.code}${activity.index}`}>
          <span onClick={() => goTo(activity)}>{activity.title}</span>
        </li>)
      }
    </ol>

    <button
      className="select-random-activity-button"
      onClick={() => {
        const randomActivityIndex = Math.round(Math.random() * (filteredActivities.length - 1));
        const activity = filteredActivities[randomActivityIndex];
        goTo(activity);
      }}
    >
      <img
        src="https://www.freeiconspng.com/thumbs/dice-png/dice-png-transparent-images--png-all-4.png"
        alt="Выбрать произвольную активность"
        title="Выбрать произвольную активность"
      />
    </button>
  </>;
}


export default ActivitiesList;
